.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #97ee9e;
  border: 1px solid #49ff02;
  border-radius: 8px;
  padding: 20px;
  max-width: 600px;
  margin: 20px auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
  text-align: center;
}

.input {
  height: 20px;
  width: 350px;
  padding: 8px;
  margin-bottom: 20px;
}
.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

.item span {
  flex-grow: 1;
  margin-right: 10px;
  font-size: 18px;
}

.buttonGroup {
  display: flex;
  gap: 10px;
}

.red-border {
  border: 1px solid red;
}

.btn,
.btnDel {
  background-color: #487eec;
  color: rgb(255, 255, 255);
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  margin: 25px;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #fcfcfc;
  color: #487eec;
}

.btn:active {
  background-color: #fafafa;
}

.btnDel:hover {
  background-color: #f94949;
}

.btnDel:active {
  background-color: #f94949;
}

.myModal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  background: rgb(0, 0, 0, 0.6);
}

.myModalContent {
  padding: 25px;
  background: #ece0e0;
  border-radius: 16px;
  min-width: 250px;
}

.myModal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}
