  .btn,
  .btnDel {
    background-color: #487eec;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    margin: 25px;
    transition: background-color 0.3s ease;
  }
  
  .btn:hover {
    background-color: #fcfcfc;
    color: #487eec;
  }
  
  .btn:active {
    background-color: #fafafa;
  }
  
  .btnDel:hover {
    background-color: #f94949;
  }
  
  .btnDel:active {
    background-color: #f94949;
  }
  
  
